<template>
  <div>
    <Row>
      <Col span="5">章节</Col>
      <Col span="6">图片名</Col>
      <Col span="4">封面</Col>
      <Col span="4">图片地址</Col>
      <Col span="2">排序</Col>
      <Col span="3">操作</Col>
    </Row>
    <Row v-for="item in list" :key="item.name" class="row-item">
      <Col class="row-col" span="5">{{item.categoryname}}</Col>
      <Col class="row-col" span="6">{{item.name}}</Col>
      <Col class="row-col" span="4">
        <img style="width: 40px;height: 40px;vertical-align: middle" :src="item.cover">
      </Col>
      <Col class="row-col" span="4">
        <p style="text-overflow: ellipsis;white-space: nowrap; max-width: 100px;">{{item.url}}</p>
      </Col>
      <Col class="row-col" span="2">{{item.sort}}</Col>
      <Col class="row-col" sapn="3">
        <span style="text-align: center;" @click="deleteGallery(item.id)">删除</span>
      </Col>
    </Row>
    <Page :total="total" :current="page" simple onchange="changePage"></Page>
  </div>
</template>

<script>

import {getGalleryList, getVideoList} from "../../../utils/api";

export default {
  name: "WidgetGalleryList",
  data(){
    return {
      list:[], //视频列表
      page:0, //当前页码
      total:0
    }
  },
  mounted() {
    this.page = 1
    this.changePage(1)
  },
  updated() {
    console.log("update");
  },
  methods:{
    changePage(page){
      this.page = page
      getGalleryList({page:page,size:10}).then(res => {
        console.log(res)
        this.list = res.data.data.list
        this.total = res.data.data.total
      }).catch(err => {
        console.log(err)
      })
    },
    //删除对应的视频
    deleteGallery(vid){
      console.log(vid)
    }
  }
}
</script>

<style scoped>
.row-item{
  line-height: 50px;
  height: 50px;
  background-color: #3ebae0;
  margin: 2px 2px 2px 2px;
}
.row-item .row-col{
  line-height: 50px;
  margin: 0px 2px;
  vertical-align: middle;
}
</style>